import React, { useState, useEffect } from 'react';
import axios from '../../axios'
import { Formik, Form, Field } from 'formik';
import { TextField, Select} from 'formik-material-ui';
import { Button,Grid , Container,FormControl,InputLabel,MenuItem, Paper } from '@material-ui/core';
import 'date-fns';
import { makeStyles } from "@material-ui/core/styles";
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    DatePicker
  } from '@material-ui/pickers';
  import moment from 'moment'
import * as yup from 'yup';
const aadhaarRegExp = /^([0-9]){12}$/;
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const pincodeRegExp = "^[1-9]{1}[0-9]{2}[0-9]{3}$";
const emailRegExp="[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?"
const validationSchema = yup.object({
  firstName: yup.string()
      .required('Student First Name Required')
      .min(2,'First Name Too Short'),
  lastName: yup.string()
      .min(3,'Last Name Too Short'),
  fFirstName: yup.string()
      .required("Father's First Name Required")
      .min(2,'First Name Too Short'), 
  mothersName: yup.string()
      .required('Mother Name Required')
      .min(3,'Mother Name too Short'),
  aadhaar: yup.string()
      .matches(aadhaarRegExp, 'Aadhaar number is not valid'),
  addressLine1: yup.string()
      .required('Enter Mohalla / Street Name')
      .min(3,'Mohalla / Street  Name too Short'),
  pinCode: yup.string()
      .required('Pincode Required')
      .matches(pincodeRegExp, 'Invalid Pincode'),
  email: yup.string().email()
      .required("Valid Email Of Student Required"),
      //.matches(emailRegExp,"invalid Email Address."),
  tehsil: yup.string()
       .required("Tehsil Name Required")
       .min(2,'Tehsil Name too Short'),
  pOccupation: yup.string()
      .required('Student First Name Required')
      .min(2,'Occupation Invalid ')
      .max(20,'Max 20 Characters'),
  pPhone: yup.string()
      .required("Mobile Number of Parent/Guardian")
      .matches(phoneRegExp, 'Mobile number is not valid')
      .min(10, "Mobile number is not valid")
      .max(10, "Mobile number is not valid"),
  fgIncome: yup.number()
      .required("Enter Guardian/Father Income")
      .positive("Income can not be negative")
      .integer("Income should not be a decimal point")
      .max(9000000, "Income is above Criteria"),
  bankBranch: yup.string()
      .required("Enter Bank Branch")
      .min(3, "Min 3 Characters")
      .max(50, "Max 50 Characters"),
  accountNumber: yup.string()
      .required("Enter J&K Bank 16 Digit A/C No  ")
      .matches(/^[0-9]{16}$/, 'A/C No must be 16 digits(0-9)'),
  });
  const useStyles = makeStyles((theme) =>({
    
    textbox: {
      backgroundColor: '#fff',
      transform: 'upper'
    }
  }));
 
export default function EditForm ({formdata}){
  const classes = useStyles();
  const [formData,setFormData]=React.useState({
    Student_ID:'',
    mobileNo:'',
    University_Form_No:'',
    boardRegNo:'',
    firstName: '',
    middleName: '',
    lastName: '',
    fFirstName: '',
    fMiddleName: '',
    fLastName: '',
    mothersName: '',
    aadhaar:'',
    dob: null,
    gender:'',
    religion:'',
    category:'',
    pOccupation:'',
    pPhone:'',
    fgIncome:'',
    incomeCategory:'',
    bankName:'',
    accountNumber:'',
    bankBranch:'',
    bankIFSC:'',
    addressLine1:'',      
    tehsil:'',
    districtName:'',
    pinCode:'',
    email:'',
    prevStream:'',
    examBody:'',
    Result:'',
    rollNo:'',
    Session:'',
    Year:'',
    maxMarks:'',
    marksObtained:'',
    courseApplied:'',
    Roll_no: '',
    photo:'',
  })

  useEffect(() => {
    setFormData(formdata)

},[]);
 
  const [doblocal, setdoblocal] = useState(null);
  const  handleDateChange=(date)=> {
  const dobdate=moment(date)
  setFormData({'dob':  dobdate.format('MM/DD/YYYY')})
  setdoblocal( dobdate.format('MM/DD/YYYY'))
  };

  return (
    <>
      <Container maxWidth='lg'>
      <Paper elevation='3'>
     <div style={{"border": '1px', "borderStyle": 'Solid', 'borderColor': '#e2e2e2', "backgroundColor": '#f2fcff', "padding": '30px'}}>
     <Formik
        initialValues={formData}
        enableReinitialize
        onSubmit={values => {
        values.dob=formData.dob
        //setFormData(values);
       // alert(JSON.stringify(values))

        setTimeout(() => {  
            axios.post('api/admissions/update/studentbiodata',JSON.stringify(values), {
              headers: {
                // Overwrite Axios's automatically set Content-Type
                'Content-Type': 'application/json',
                'Accept': "application/json"
              }})
              .then((res) => {
                if(res.status===200 ){
                  alert("FOrm Updated Successfully");
                }
              })
              .catch(function (error) {
                alert("Error occured:" + error);
              })
          }, 500);
        }}
        validationSchema={validationSchema}
      >
      {({values}) => (
        <Form>
          <Grid container  direction={'row'} spacing={3}>
          <Grid item lg={3} xs={12}>
               <div style={{textAlign: 'left'}}> 
              <label className='formreviewLabel'><strong>College Form No</strong>
                <div className='formreview' style={{color: '#888b8c'}}>
                  {values.Student_ID}
                </div>
            </label>
          </div>
            
            </Grid>
           <Grid item lg={3} xs={12}>
           <div style={{textAlign: 'left'}}> 
              <label className='formreviewLabel'><strong>Verified Mobile No</strong>
                <div className='formreview' style={{color: '#888b8c'}}>
                  {values.mobileNo}
                </div>
            </label>
          </div>
            
               </Grid>
               <Grid item lg={3} xs={12}>
              <div style={{textAlign: 'left'}}> 
              <img src={new Buffer(values.photo).toString()} heigh='100' width='100' className="rounded mx-auto d-block " alt="aligment"/>
              </div>
              </Grid>
               <Grid item lg={3} xs={12}>
              <div style={{textAlign: 'left'}}> 
                <label className='formreviewLabel'><strong>Verified Email</strong>
                    <div className='formreview' style={{color: '#888b8c'}}>
                      {values.email}
                    </div>
                </label>
              </div>
                
               </Grid>
               <Grid item lg={3} xs={12}>
              <div style={{textAlign: 'left'}}> 
                <label className='formreviewLabel'><strong>Board Reg. No</strong>
                    <div className='formreview' style={{color: '#888b8c'}}>
                      {values.boardRegNo}
                    </div>
                </label>
              </div>
                
               </Grid>
               <Grid item lg={3} xs={12}>
               <div style={{textAlign: 'left'}}> 
              <label className='formreviewLabel'><strong>University Form No</strong>
                <div className='formreview' style={{color: '#888b8c'}}>
                  {values.University_Form_No}
                </div>
            </label>
          </div>
            
            </Grid>
            <Grid item lg={3} xs={12}>
               <div style={{textAlign: 'left'}}> 
              <label className='formreviewLabel'><strong>College Roll No</strong>
                <div className='formreview' style={{color: '#888b8c'}}>
                  {values.Roll_no}
                </div>
            </label>
          </div>
            
            </Grid>
            </Grid>
        <div style={{ color: '#283593',borderBottom: '2px solid #283593', borderLeft: '13px solid #283593', padding: '10px', fontSize: '20px', fontWeight: '500', marginBottom: '15px'}}>1-Student Personal Details   </div> 
        <div className='text-danger'>Enter The Details as Per Matriculation Certificate ( *  Fields are Mandatory)</div>
      <Grid container  direction={'row'} spacing={3}>
        <Grid item lg={4} xs={6}> <Field
           component={TextField}
           name="firstName"
           className={classes.textbox}          
           type="text"
           label="Student First Name"
           variant="outlined" 
           fullWidth
           required
           margin="dense"
          /></Grid>
        <Grid item lg={4} xs={6}>
        <Field 
            component={TextField}
            name="middleName"          
            type="text"
            className={classes.textbox}
            label="Student Middle Name"
            variant="outlined" 
            fullWidth
            margin="dense"
            
          />
          </Grid>
          <Grid item lg={4} xs={6}> <Field 
             component={TextField}
             className={classes.textbox}
             name="lastName"          
             type="text"
             label="Student Last Name"
             variant="outlined"
             fullWidth
             margin="dense"
          /></Grid>
          <Grid item lg={4} xs={6}> <Field
           component={TextField}
           className={classes.textbox}
           name="fFirstName"          
           type="text"
           label="Father's First Name"
           variant="outlined"
           fullWidth
           required
           margin="dense"
          /></Grid>
        <Grid item lg={4} xs={6}>
        <Field 
            component={TextField}
            className={classes.textbox}
            name="fMiddleName"          
            type="text"
            label="Father's Middle Name"
            variant="outlined" 
            fullWidth
            margin="dense"
          />
          </Grid>
          <Grid item lg={4} xs={6}> <Field 
             component={TextField}
             className={classes.textbox}
             name="fLastName"          
             type="text"
             label="Father's Last Name"
             variant="outlined" 
             fullWidth
             margin="dense"
          /></Grid>
           <Grid item lg={4} xs={6}> <Field
           component={TextField}
           className={classes.textbox}
           name="mothersName"          
           type="text"
           label="Mother's Name"
           variant="outlined" 
           fullWidth
           required
           margin="dense"
          /></Grid>
           <Grid item lg={4} xs={6}> <Field
           component={TextField}
           className={classes.textbox}
           name="aadhaar"          
           type="text"
           label="Aadhaar Number"
           variant="outlined"
           fullWidth
           margin="dense"
          /></Grid>
        
          <Grid item lg={3} xs={6}>
          <MuiPickersUtilsProvider utils={DateFnsUtils} required >
                  <DatePicker
                          autoOk
                          emptyLabel='dd/mm/yyyy'
                          animateYearScrolling
                          required
                          className={classes.textbox}
                          fullWidth
                          inputVariant="outlined"
                          disableFuture
                          label="Date Of Birth"
                          format="dd/MM/yyyy"
                          value={formData.dob}
                          name='dob'
                          margin="dense"
                          onChange={date => handleDateChange(date)}
                        />
              </MuiPickersUtilsProvider>

          </Grid>
          <Grid item lg={4} xs={6}> 
         
          <FormControl variant="outlined" fullWidth required margin='dense'>
              <InputLabel htmlFor="gendersimple">Gender</InputLabel>
              <Field
                component={Select}
                className={classes.textbox}
                name="gender"
                label='Gender'
                inputProps={{
                  id: 'gendersimple',
                }}
              >
                <MenuItem value={'Male'}>Male</MenuItem>
                <MenuItem value={'Female'}>Female</MenuItem>
                <MenuItem value={'Transgender'}>Transgender</MenuItem>
              </Field>
            </FormControl>
          </Grid>
          <Grid item lg={4} xs={6}> 
          <FormControl variant="outlined" fullWidth required margin='dense'>
              <InputLabel htmlFor="Religion-simple">Religion</InputLabel>
              <Field
                component={Select}
                className={classes.textbox}
                name="religion"
                label='Religion'
                inputProps={{
                  id: 'Religion-simple',
                }}
              >
                <MenuItem value={'Islam'}>Islam</MenuItem>
                <MenuItem value={'Hinduism'}>Hinduism</MenuItem>
                <MenuItem value={'Sikhism'}>Sikhism</MenuItem>
                <MenuItem value={'Cheristianity'}>Cheristianity</MenuItem>
                <MenuItem value={'Budism'}>Budism</MenuItem>
                <MenuItem value={'Others'}>Others</MenuItem>
              </Field>
            </FormControl>
          </Grid>
          <Grid item lg={4} xs={6}>
          <FormControl variant="outlined" fullWidth required margin='dense'>
              <InputLabel htmlFor="Category-simple">Category</InputLabel>
              <Field
                component={Select}
                className={classes.textbox}
                name="category"
                label='Category'
                inputProps={{
                  id: 'Category-simple',
                }}
              >
                <MenuItem value={'Open Merit'}>Open Merit</MenuItem>
                <MenuItem value={'Social Cast'}>Social Cast</MenuItem>
                <MenuItem value={'Social Tribe'}>Social Tribe</MenuItem>
                <MenuItem value={'Backward Class'}>Backward Class</MenuItem>
                <MenuItem value={'Other Class'}>Other Class</MenuItem>
              </Field>
            </FormControl>
          </Grid>
                  
          <Grid item lg={12} xs={12}>
          <div style={{ color: '#283593',borderBottom: '2px solid #283593', borderLeft: '13px solid #283593', padding: '10px', fontSize: '20px', fontWeight: '500', marginBottom: '15px'}}>2-Student Address Details   </div> 
         
            </Grid>
          <Grid item lg={6} xs={12}>
        <Field 
            component={TextField}
            className={classes.textbox}
            name="addressLine1"          
            type="text"
            label="Address"
            variant="outlined"
            fullWidth
            required
            margin="dense"
          />
          </Grid>
          
          <Grid item lg={3} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="tehsil"          
           type="text"
           label="Tehsil"
           required
           variant="outlined"
           fullWidth
           margin="dense"
          /></Grid>
        <Grid item lg={3} xs={6}>
        <FormControl variant="outlined"  required fullWidth margin="dense">
            <InputLabel htmlFor="districtname-select">District</InputLabel>
            <Field
            component={Select}
            className={classes.textbox}
            label="District"
            name='districtName'
            inputProps={{
               
                id: 'districtname-select',
            }}
            >
          
            <MenuItem value={'Pulwama'}>Pulwama</MenuItem>
            <MenuItem value={'Shopian'}>Shopian</MenuItem>
            <MenuItem value={'Budagam'}>Budgam</MenuItem>
            <MenuItem value={'Srinagar'}>Srinagar</MenuItem>
            <MenuItem value={'Anantnag'}>Anantnag</MenuItem>
            <MenuItem value={'Kulgam'}>Kulgam</MenuItem>
            </Field>
        </FormControl>
          </Grid>
              
           
                <Grid item lg={3} xs={6}> 
                <Field
                    component={TextField}
                    className={classes.textbox}
                    name="pinCode"          
                    type="text"
                    label="Pin Code"
                    variant="outlined"
                    fullWidth
                    required
                    margin="dense"
                /></Grid>   
<Grid item lg={12} xs={12}>
<div style={{ color: '#283593',borderBottom: '2px solid #283593', borderLeft: '13px solid #283593', padding: '10px', fontSize: '20px', fontWeight: '500', marginBottom: '15px'}}>
      3-Student Father's/Guardian's Income Details
  </div>
          
          </Grid>
             
        <Grid item lg={4} xs={12}>
        <Field 
            component={TextField}
            className={classes.textbox}
            name="pOccupation"          
            type="text"
            label="Parent's/Guardian's Occupation"
            variant="outlined"
            fullWidth
            margin="dense"
            required
          />
          </Grid>
          <Grid item lg={4} xs={12}> <Field 
             component={TextField}
             className={classes.textbox}
             name="pPhone"          
             type="text"
             label="Parent's/Guardian's Phone No"
             variant="outlined"
             fullWidth
             margin="dense"
             required
          /></Grid>
          <Grid item lg={4} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="fgIncome"          
           type="text"
           label="Father's/Guardian's Monthly Income"
           variant="outlined"
           fullWidth
           margin="dense"
           required
          /></Grid>
        <Grid item lg={4} xs={12}>
        <FormControl variant="outlined" fullWidth required margin='dense'>
            <InputLabel htmlFor="incomeCategory-select">Income Category</InputLabel>
            <Field
              component={Select}
              className={classes.textbox}
              label="Income Category"
              name="incomeCategory"
              inputProps={{
                id: 'incomeCategory-select',
              }}
            >
              <MenuItem value={'APL'}>APL</MenuItem>
              <MenuItem value={'BPL'}>BPL</MenuItem>
              <MenuItem value={'AAY'}>AAY</MenuItem>
              <MenuItem value={'PHH'}>PHH</MenuItem>
            </Field>
         </FormControl>
          </Grid>

          <Grid item lg={12} xs={12}>
          <div style={{ color: '#283593',borderBottom: '2px solid #283593', borderLeft: '13px solid #283593', padding: '10px', fontSize: '20px', fontWeight: '500', marginBottom: '15px'}}>
          4-Student's Bank Details
         </div>
          
          </Grid>
          <Grid item lg={4} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="bankName"          
           type="text"
           label="Bank Name*"
           variant="outlined"
           fullWidth
           margin="dense"
          /></Grid>
           <Grid item lg={4} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="accountNumber"          
           type="text"
           required
           label="Bank Account No"
           variant="outlined"
           fullWidth
           margin="dense"
          /></Grid>
           <Grid item lg={4} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="bankBranch"          
           type="text"
           label="Bank Branch"
           variant="outlined"
           fullWidth
           required
           margin="dense"
          /></Grid>
           <Grid item lg={4} xs={12}> </Grid>     
          <Grid item lg={3} xs={1}></Grid>
          <Grid item lg={12} xs={12}>
          <div style={{ color: '#283593',borderBottom: '2px solid #283593', borderLeft: '13px solid #283593', padding: '10px', fontSize: '20px', fontWeight: '500', marginBottom: '15px'}}>
          Student's 12th Class Details
         </div>
          
          </Grid>
          <Grid item lg={12} xs={12}>
          <FormControl variant="outlined" fullWidth required margin="dense">
                <InputLabel htmlFor="prevStream-select">Course/ Stream</InputLabel>
                <Field
                component={Select}
                className={classes.textbox} 
                name= 'prevStream'
                label="Course / Stream"
                inputProps={{
                    id: 'prevStream-select',
                }}
                >
               
                <MenuItem value={'Arts'}>Arts</MenuItem>
                <MenuItem value={'Science'}>Science</MenuItem>
                <MenuItem value={'Commerce'}>Commerce</MenuItem>
                <MenuItem value={'Non Medical'}>Non Medical</MenuItem>
                <MenuItem value={'Others'}>Others</MenuItem>
                </Field>
            </FormControl>
           
            </Grid>
            <Grid item lg={3} xs={3}>
             <FormControl variant="outlined" required fullWidth margin="dense">
                <InputLabel htmlFor="examBody-select">Exam Body</InputLabel>
                <Field
                component={Select}
                className={classes.textbox} 
                name='examBody'
                label="Exam Body"
                inputProps={{
                    id: 'examBody-select',
                }}
                >
                <MenuItem value={'JKBOSE'}>JK BOSE</MenuItem>
                <MenuItem value={'CBSE'}>CBSE</MenuItem>
                <MenuItem value={'NCRT'}>NCRT</MenuItem>
                <MenuItem value={'Others'}>Others</MenuItem>
                </Field>
            </FormControl>
           
            </Grid>
            <Grid item lg={2} xs={3}>
             <FormControl variant="outlined" required fullWidth margin="dense">
                <InputLabel htmlFor="Result-select">Result</InputLabel>
                <Field
                component={Select}
                className={classes.textbox} 
                name='Result'
                label="Result"
                inputProps={{
                    id: 'Result-select',
                }}
                >
                <MenuItem value={'Pass'}>Pass</MenuItem>
                <MenuItem value={'Fail'}>Fail</MenuItem>
                <MenuItem value={'Reappear'}>Reappear</MenuItem>
                <MenuItem value={'Awaited'}>Awaited</MenuItem>
                </Field>
            </FormControl>
            </Grid>
            <Grid item lg={2} xs={12}>
            <Field
             component={TextField}
             className={classes.textbox} 
             name="rollNo"          
             type="text"
             label="Roll No"
             variant="outlined"
             fullWidth
             required
             margin="dense"
            
            />
            </Grid>
            <Grid item lg={2} xs={3}>
             <FormControl variant="outlined" required fullWidth margin="dense">
                <InputLabel htmlFor="Session-select">Session</InputLabel>
                <Field
                component={Select}
                className={classes.textbox} 
                name= 'Session'
                label="Session"
                inputProps={{
                    id: 'Session-select',
                }}
                >
                <MenuItem value={'Annual'}>Annual</MenuItem>
                <MenuItem value={'Biannual'}>Biannual</MenuItem>
                </Field>
            </FormControl>
            </Grid>
            <Grid item lg={3} xs={12}>
            <Field
             component={TextField}
             className={classes.textbox} 
             name="Year"          
             type="text"
             label="Year Of Passinng"
             variant="outlined"
             fullWidth
             required
             margin="dense"
            
            />
            </Grid>
            <Grid item lg={2} xs={12}>
            <Field
             component={TextField}
             className={classes.textbox} 
             name="maxMarks"          
             type="text"
             label="Max Marks"
             variant="outlined"
             fullWidth
             required
             margin="dense"
            
            />
               </Grid>
                  <Grid item lg={2} xs={12}>
            <Field
             component={TextField}
             className={classes.textbox} 
             name="marksObtained"          
             type="text"
             label="Marks Obtained"
             variant="outlined"
             fullWidth
             required
             margin="dense"
            
            />
         
            </Grid>
            
           
            <Grid item lg={9} xs={12}>
            
            <Button
              type='submit'
              variant='contained'
              color='primary'
              fullWidth
              style={{height: '2.5rem'}}
             
            >
            Update
            </Button>
          
         
      
           
          </Grid>
            <Grid item lg={3} xs={12}>
           
          
          </Grid>
      
          </Grid>
        </Form>
       )}
      </Formik>
      </div>
      </Paper>
      </Container>
    </>
  );
};
